label.field {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-align: center;
        align-items: center;
    margin-bottom: 1em;
}

.field span {
    margin: 1em .5em 0 .5em;
}

label.field input {
    background: white;
    font-weight: 700;
    border: 1px solid #aaa;
    border-radius: 5px;
    padding: .5em 1em;
    -ms-flex: 1 0 120px;
        flex: 1 0 120px;
    width: 100%;
    outline: none;
}

label.field:hover input {
    border: 1px solid #000;
}

label.field input:disabled {
    opacity: .5;
    cursor: wait;
}

.cmsForm {
    max-width: 450px;
    margin: 0 auto;
    background: #eee;
    padding: 5%;
    border: 1px solid #aaa;
    border-radius: 5%;
}


